.divider{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 20px 10px; */
}
.divider p{
    display: block;
    flex: 0.7 1;
    padding: 0;
    margin-top: 25px;
    font-size: 26px;
    font-weight: 600;
}
.left-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}
.right-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}


.btn{
    border:1px solid #C09621;
    padding: 10px;
    width: 40%;
    background-color: transparent;
    color: #C09621;
    cursor: pointer;
    font-weight: 500;
    font-size: 15px;
    border-radius: 10px;
    text-align: center;
}

.slider-btn{
    -webkit-animation: breathing 5s ease-out infinite normal;
    animation: breathing 5s ease-out infinite normal;
    -webkit-font-smoothing: antialiased;
    /* transition: all 1.5s ease-out; */
}




/* -web-kit animation */

@-webkit-keyframes breathing {
    0% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
  
    25% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  
    60% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
  
    100% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
  }
  
  @keyframes breathing {
    0% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
  
    25% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  
    60% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
  
    100% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
  }
  
